@import (less) "color.less";
@footer-height: 6.250em; //non è meglio? 100px;
@footer-empty-height: 3.125em; //non è meglio 50px;??


.footer-content {
    height: @footer-height;
}

.empty-footer {
    height: @footer-empty-height ;
  }


/**Mixins per visualizzazione bordi*/

//eliminato per semplicità il bordo-effetto ombra
//border-right: 1px solid @light_black;
.top-left-border {
    border-top: none;
    border-bottom: none;
    border-left: 1px solid @gray ;
}

.bottom-right-border {
    border-top: none;
    border-right: 1px solid @gray ;
    border-bottom: none;
}


/**fine Mixins per visualizzazione bordi*/

//regole per tutte le dimensioni (in particolare per -md e -lg)
.footer-content-left,
.footer-content-center-l,
.footer-content-center-r,
.footer-content-right {
    display: table;
    height: inherit;
    color: @white;
    >div {
        display: table-cell;
        vertical-align: middle;
        text-align: center;
    }
}

.footer-content-left {
    padding-left: 10px;
    padding-right: 10px;
    text-transform: uppercase;
    span {
        cursor: pointer;
    }
}

.footer-content-center-r {
    button {
        background-color: @darkest_gray !important;
        border: none !important;
        &:hover {
            background-color: @white !important;
            color: @darkest_gray !important;
        }
    }
}

.footer-content-right {
    border-right: none;
}

.footer-link {
    font-family: Arial;
    font-size: 12px;
    white-space: nowrap;
    &:hover {
        text-decoration: underline;
    }
}

.truncate-link {
    display: inline-block;
    vertical-align: middle;
    max-width: 10em;
    text-overflow: ellipsis;
    overflow: hidden;
}

.footer-languages {
    font-size: 1.500em;
    .ico-footer-contrasto-1 {
        cursor: pointer;
    }
    .footer-lang {
        cursor: pointer;
        font-family: Arial;
        font-size: 12px;
        margin-left: 0 0.313em 0 0;
    }
    .ico-footer-contrasto-2 {
        cursor: pointer;
    }
    >div {
        display: inline-block;
        padding: 0 0.313em 0 0.313em;
    }
}

.footer-contrasto {
    font-size: 1.500em;
    .ico-footer-contrasto-1 {
        cursor: pointer;
    }
    .ico-footer-contrasto-2 {
        cursor: pointer;
    }
    >div {
        display: inline-block;
        padding-left: 0 0.313em 0 0.313em;
        &:hover {
            background-color: @white;
            color: @darkest_gray;
        }
    }
}

.footer-testo {
    font-size: 1.500em;
    cursor: pointer;
    .ico-footer-testo-1 {
        font-size: 0.5em;
    }
    .ico-footer-testo-2 {
        font-size: 0.8em;
    }
    >div {
        display: inline-block;
        padding: 0 0.313em 0 0.313em;
        &:hover {
            background-color: @white;
            color: @darkest_gray;
        }
    }
}

.footer-testo-selection,
.footer-languages-selection,
.footer-contrasto-selection {
    cursor: pointer;
    font-size: 0.625em;
    margin-top: -0.625em;
}

.footer-languages-selection {
    margin: -0.625em 1.313em 0 0.313em;
}

//impila contrasto e dimensione testo
.footer-content-right {
    >div {
        //  display: block;
    }
}

.footer {
    background-color: @darkest_gray; 
    //padding-top: 0.625em;
    //padding-bottom: 0.625em;
    margin-top: 1.875em !important;
    color: @white;
    border-bottom: 0.375em solid @black; 
    //position: relative;
    width: 100%;
    z-index: 2; 
    //position: absolute;
    //right: 0;
    //left: 0;
    bottom: 0;
    box-sizing: content-box;
}

@media (min-width: 768px) {
    .footer {
        margin-bottom: 0;
        margin-right: 2.500em;
    }
 
}

@media (min-width: 992px) {
    .footer {
        margin-top: 1.250em;
        margin-right: 12.500em;
    }
}

//regole per la dimensione bootstrap col-xs
@media (max-width: 767px) {
    .top-left-border {
        border-top: 1px solid @gray ;
        border-right: none;
        border-bottom: none;
        border-left: none;
    }
    .bottom-right-border {
        border-top: none;
        border-right: none;
        border-bottom: 1px solid @gray ;
        border-left: none;
    }
    .footer {
        height: (@footer-height*4);
        .row {
            padding: 0 15px 0 15px;
        }
    }
    .empty-footer {
    height: @footer-empty-height !important ;
     > div {
      display:none;
    }
  }
    .footer-content-right {
        padding-top: 15px;
    }
}

//regole per la dimensione bootstrap col-sm
@media (min-width: 768px) and (max-width: 991px) {
    .top-left-border {
        border-top: 1px solid @gray;
        border-right: none;
        border-bottom: none;
        border-left: 1px solid @gray;
    }
    .bottom-right-border {
        border-top: none;
        border-right: 1px solid @gray;
        border-bottom: 1px solid @gray;
        border-left: none;
    }
    .footer-content-left.bottom-right-border {
        border-bottom: none !important;
    }
    .footer-content-center-l.top-left-border {
        border-top: none !important;
        border-right: none !important;
    }
    .footer-content-center-r.top-left-border {
        border-bottom: none !important;
        border-left: none !important;
    }
    .footer-content-right {
        border-right: none !important;
        border-bottom: none !important;
    }
    .footer {
        height: (@footer-height*2);
        .row {
            padding: 0 15px 0 15px;
        }
    }
    .empty-footer {
        height: @footer-empty-height !important ;
        > div {
        display:none;
        }
    }
}

//regole per il contrasto
.contrasto-on {
    background-color: @black;
    .footer-content-left {
        color: @white;
    }
    .footer-content-center-r {
        button {
            background-color: @black !important;
            &:hover {
                background-color: @white !important;
                color: @gray !important;
            }
        }
    }
    .footer-contrasto {
        color: @light_gray;
        >div {
            &:hover {
                background-color: @black;
                color: @white;
            }
        }
    }
    .footer-testo {
        color: @light_gray;
        >div {
            &:hover {
                background-color: @black;
                color: @white;
            }
        }
    }
}

.button-icon {
    font-size: 1.70em !important;
    display: inline-block;
    vertical-align: middle;
    padding-right: 0.20em;
}

.button {
    font-weight: bold;
    font-size: 0.750em;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    text-transform: uppercase;
    height: 3.3334em;
    min-width: 6.875em;
    box-shadow: none !important;
}